        p El guion turístico constituye la columna vertebral del recorrido. Define qué se comunica, cómo, cuándo y con qué propósito, organizando la información de manera coherente, didáctica y emocionalmente significativa. De acuerdo con Morales (2019), un guion bien estructurado garantiza la coherencia narrativa, el uso eficiente del tiempo y la conexión emocional del visitante con el tema central. Aunque el guion es un documento planificado, debe considerarse una herramienta flexible y evolutiva, capaz de adaptarse a factores como la segmentación del público, el contexto histórico, las condiciones del entorno y la especialidad del guía. No obstante, cada guion turístico tiene un esquema que debe componerse de los elementos presentados en la siguiente tabla. 

